/* stylelint-enable */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
:root {
  --black: rgba(0, 0, 0, 1);
  --black-alhpa-1: rgba(0, 0, 0, 0.1);
  --black-alpha-2: rgba(0, 0, 0, 0.2);
  --black-alpha-3: rgba(0, 0, 0, 0.3);
  --black-alpha-6: rgba(0, 0, 0, 0.6);
  --grey: rgba(68, 68, 68, 1);
  --grey-2: rgba(236, 236, 236, 1);
  --grey-3: rgba(229, 229, 229, 1);
  --white: rgba(255, 255, 255, 1);
  --white-alpha-3: rgba(255, 255, 255, 0.3);
  --white-2: rgba(249, 249, 249, 1);
  --white-3: rgba(241, 241, 241, 1);
  --cyan: rgba(99, 209, 187, 1);
  --cyan-alpha-3: rgba(99, 209, 187, 0.3);
  --cyan-2: rgba(86, 195, 178, 1);
  --cyan-3: rgba(98, 209, 186, 1);
  --cyan-4: rgba(70, 193, 174, 1);
}

body {
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 17px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: var(--grey);
  background-color: var(--white);
}

img {
  display: block;
}

.show-main-nav::before {
  content: "";
  position: absolute;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(45, 52, 64, 0.5);
  opacity: 0;
}

.show-main-nav--active {
  overflow: hidden;
}
.show-main-nav--active::before {
  z-index: 2;
  opacity: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 520px;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    max-width: 968px;
    padding-right: 50px;
    padding-left: 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1150px;
    padding-right: 50px;
    padding-left: 50px;
  }
}

.button {
  display: inline-block;
  padding: 16px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  font-family: "Open Sans", "Arial", sans-serif;
  text-align: center;
  color: var(--grey);
  text-transform: uppercase;
  text-decoration: none;
  background-color: transparent;
  border: 2px solid var(--grey);
  cursor: pointer;
  transition: all 0.8s ease;
}
.button:hover {
  color: var(--white);
  background-color: var(--grey);
}
.button:active {
  color: var(--white-alpha-3);
}

.header {
  position: relative;
  z-index: 10;
}
@media (max-width: 767px) {
  .header {
    background-color: var(--white);
  }
}
@media (min-width: 768px) {
  .header {
    margin-bottom: -50px;
  }
}
@media (min-width: 1150px) {
  .header {
    margin-bottom: -65px;
  }
}
@media (max-width: 767px) {
  .header::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 100vw;
    border-bottom: 1px solid var(--black-alhpa-1);
  }
}

.header__inner {
  position: relative;
}

@media (max-width: 767px) {
  .header__logo {
    display: flex;
    justify-content: center;
    padding: 22px 0;
  }
}
@media (min-width: 768px) {
  .header__logo {
    position: absolute;
    top: 22px;
    left: 0;
  }
}
@media (min-width: 1150px) {
  .header__logo {
    position: absolute;
    top: 9px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__hamburger {
  position: absolute;
  top: 24px;
  right: -4px;
  width: 20px;
  height: 14px;
  padding: 15px;
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  transition-delay: 0.1s, 0s;
}
@media (min-width: 768px) {
  .header__hamburger {
    display: none;
  }
}

.header__hamburger--no-js {
  display: none;
}

.header__hamburger--open:hover {
  transform: rotate(90deg);
}

.header__hamburger-inner {
  position: absolute;
  top: 45%;
  right: 5px;
  left: 5px;
  height: 2px;
  background-color: var(--black);
  transition: background 0s 0.3s;
}

.header__hamburger-inner::before,
.header__hamburger-inner::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--black);
  transition-delay: 0.3s, 0s;
  transition-duration: 0.3s, 0.3s;
  transition-property: top, transform;
}

.header__hamburger-inner::before {
  top: -6px;
}

.header__hamburger-inner::after {
  top: 6px;
}

.header__hamburger--open .header__hamburger-inner {
  background-color: transparent;
}

.header__hamburger--open .header__hamburger-inner::before,
.header__hamburger--open .header__hamburger-inner::after {
  transition-delay: 0s, 0.3s;
}

.header__hamburger--open .header__hamburger-inner::before {
  top: 0;
  transform: scale(1.3) rotate(45deg);
}

.header__hamburger--open .header__hamburger-inner::after {
  top: 0;
  transform: scale(1.3) rotate(-45deg);
}

.footer {
  background: #f1f1f1;
}

.footer__inner {
  display: flex;
  padding: 27px 0;
}
@media (max-width: 767px) {
  .footer__inner {
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .footer__inner {
    justify-content: space-between;
    padding: 51px 0;
  }
}

@media (max-width: 767px) {
  .footer__logo {
    display: none;
  }
}

.footer__logo-img {
  transition: all 0.5s ease;
}
.footer__logo-img:hover {
  fill: var(--cyan-4);
}
.footer__logo-img:active {
  opacity: 0.3;
}

.footer__social-list {
  justify-content: center;
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-bottom: 26px;
  }
}
@media (min-width: 768px) {
  .footer__social-list {
    align-items: center;
  }
}

.footer__madeby-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
  text-decoration: none;
}
@media (max-width: 767px) {
  .footer__madeby-link {
    padding-top: 23px;
  }
}
@media (min-width: 768px) {
  .footer__madeby-link {
    align-items: unset;
  }
}
@media (max-width: 767px) {
  .footer__madeby-link::before {
    content: "";
    position: absolute;
    top: 0;
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--grey-3);
  }
}

.footer__madeby-text {
  margin-right: 27px;
}
@media (min-width: 768px) {
  .footer__madeby-text {
    margin-top: 7px;
  }
}
@media (min-width: 1150px) {
  .footer__madeby-text {
    margin-right: 30px;
  }
}

.footer__madeby-img {
  transition: all 0.5s ease;
  fill: var(--cyan);
}

.footer__madeby-link:hover .footer__madeby-img {
  fill: var(--cyan-4);
}

.footer__madeby-link:active .footer__madeby-img {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .logo__image {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .logo__image {
    width: 149px;
    height: 72px;
  }
}

.social-list {
  display: flex;
  list-style: none;
  column-gap: 31px;
}

.social-list__link:hover {
  fill: var(--cyan-4);
}

.social-list__link:active {
  opacity: 0.3;
}

.social-list__image {
  transition: all 0.5s ease;
}

@media (max-width: 767px) {
  .navigation {
    position: fixed;
    right: 0;
    left: 0;
    overflow: hidden;
    visibility: hidden;
    pointer-events: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    grid-template-columns: 10fr 11fr;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .navigation::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;
    display: block;
    width: 300vw;
    height: 300vw;
    margin-top: -150vw;
    margin-left: -150vw;
    background-color: var(--white);
    border-radius: 50%;
    transform: scale(0);
    transition: all 0.8s ease;
  }
}

.navigation--open,
.navigation--no-js {
  visibility: visible;
  pointer-events: auto;
}
.navigation--open::before,
.navigation--no-js::before {
  transform: scale(1);
  transition-duration: 0.8s;
}

.navigation--open .navigation__item,
.navigation--no-js .navigation__item {
  opacity: 1;
  transition: opacity 0.3s ease 0.3s;
}

.navigation--mobile .navigation__item {
  transition-duration: 0.3s;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media (min-width: 768px) {
  .navigation__list--user {
    position: relative;
    grid-column: 2/3;
    justify-self: end;
  }
}
@media (min-width: 768px) {
  .navigation__list--user::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 200vw;
    border-bottom: 1px solid var(--black-alhpa-1);
    transform: translateX(50%);
  }
}

@media (min-width: 768px) {
  .navigation__list--main {
    padding-top: 16px;
    font-size: 15px;
    column-gap: 23px;
    grid-row: 2/3;
  }
}
@media (min-width: 1150px) {
  .navigation__list--main {
    justify-content: start;
    margin-left: -36px;
    padding: 0;
    font-size: 17px;
    column-gap: 0;
    grid-row: 1/2;
  }
}

.navigation__list--more {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__list--more {
    display: grid;
    font-size: 15px;
    column-gap: 64px;
    justify-self: start;
  }
}

@media (max-width: 767px) {
  .navigation__item {
    position: relative;
    opacity: 0;
  }
}
@media (max-width: 767px) {
  .navigation__item::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    display: block;
    width: 200vw;
    border-bottom: 1px solid var(--black-alhpa-1);
    transform: translateX(50%);
  }
}

@media (min-width: 768px) {
  .navigation__list--user .navigation__item {
    border-left: 1px solid var(--black-alhpa-1);
  }
}

@media (min-width: 1150px) {
  .navigation__list--main .navigation__item:nth-child(2n+1) {
    border-right: 1px solid var(--black-alhpa-1);
  }
}

@media (min-width: 1150px) {
  .navigation__list--user .navigation__item:last-child {
    grid-column: 2/3;
  }
}

.navigation__link {
  display: block;
  padding-top: 28px;
  padding-bottom: 27px;
  color: var(--black);
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}
.navigation__link:hover {
  color: var(--black-alpha-6);
}
.navigation__link:active {
  color: var(--black-alpha-3);
}

@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__list--main .navigation__link {
    padding: 5px 0;
  }
}
@media (min-width: 1150px) {
  .navigation__list--main .navigation__link {
    padding-right: 36px;
    padding-left: 36px;
  }
}

@media (min-width: 1150px) {
  .navigation__list--more .navigation__link {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.navigation__text {
  position: relative;
}
@media (max-width: 767px) {
  .navigation__text {
    display: block;
    width: 200px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 46px;
  }
}

.navigation__list--user .navigation__text::before {
  content: "";
  position: absolute;
  display: inline-block;
  background-color: var(--black-alpha-2);
}
@media (min-width: 768px) {
  .navigation__list--user .navigation__text::before {
    position: static;
    padding-right: 31px;
    padding-left: 31px;
  }
}

@media (min-width: 768px) {
  .navigation__text--search {
    font-size: 0;
    line-height: 0;
  }
}
.navigation__text--search::before {
  top: 4px;
  left: 2px;
  width: 18px;
  height: 18px;
  mask: url("../img/interface/other/icon-search.svg") no-repeat center;
}

.navigation__text--cart::before {
  top: 3px;
  left: 0;
  width: 21px;
  height: 18px;
  mask: url("../img/interface/other/icon-cart.svg") no-repeat center;
}

.navigation__link:hover .navigation__text::before {
  background-color: var(--cyan);
}

.navigation__link:active .navigation__text::before {
  background-color: var(--cyan-alpha-3);
}

.navigation__advantage {
  display: none;
}
@media (min-width: 768px) {
  .navigation__advantage {
    display: block;
    margin: 20px 0 0 0;
    font-size: 15px;
    line-height: 24px;
    justify-self: end;
  }
}

