  }
}

.form {
  padding: 17px 0 55px;
}
@media (min-width: 768px) {
  .form {
    padding: 67px 0 50px;
  }
}
@media (min-width: 1150px) {
  .form {
    padding-top: 118px;
  }
}

.form__lead-text {
