.form__legend,
.form__sign-label {
  position: relative;
  width: fit-content;
  min-width: 40px;
  padding-right: 20px;
  font-weight: 700;
  line-height: 16px;
  color: var(--cyan);
  text-transform: uppercase;
}
@media (min-width: 768px) {
