.form__sign-label {
    padding-top: 16px;
  }
}
@media (min-width: 1150px) {
  .form__legend,
.form__sign-label {
    min-width: 60px;
    padding-left: 62px;
  }
}
@media (max-width: 767px) {
  .form__legend::after,
.form__sign-label::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 100%;
    z-index: -1;
    display: block;
    width: 100vw;
    height: 1px;
    background-color: var(--cyan);
  }
}

.form__radio {
  display: flex;
  width: 40px;
  height: 40px;
  margin: 0;
  border: 2px solid var(--grey);
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  appearance: none;
  flex: 0 0 auto;
  order: -1;
}
@media (min-width: 768px) {
  .form__radio {
    width: 50px;
    height: 50px;
  }
}
.form__radio::before {
  content: "";
  display: block;
  width: 14px;
  height: 14px;
  margin: auto;
  background-color: var(--cyan-3);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
@media (min-width: 768px) {
